"use client"

import React, { createContext, useContext, useState, useEffect } from "react"
import PeraWalletConnect from "@perawallet/connect"

interface WalletContextType {
  isConnected: boolean
  walletAddress: string | null
  connect: () => Promise<void>
  disconnect: () => void
}

const WalletContext = createContext<WalletContextType | undefined>(undefined)

export function WalletProvider({ children }: { children: React.ReactNode }) {
  const [isConnected, setIsConnected] = useState(false)
  const [walletAddress, setWalletAddress] = useState<string | null>(null)
  const [peraWallet, setPeraWallet] = useState<PeraWalletConnect | null>(null)

  useEffect(() => {
    // initialize Pera wallet instance once
    const pera = new PeraWalletConnect({ shouldShowSignTxnToast: true })
    setPeraWallet(pera)

    // try to reconnect existing session
    pera.reconnectSession()
      .then((accounts: string[] | undefined) => {
        if (accounts && accounts.length > 0) {
          setWalletAddress(accounts[0])
          setIsConnected(true)
        }
      })
      .catch(() => {
        // ignore
      })

    return () => {
      // noop
    }
  }, [])

  const connect = async () => {
    try {
      if (!peraWallet) {
        console.error("Pera wallet not initialized")
        return
      }
      // open the Pera Wallet connector
      const accounts = await peraWallet.connect()
      if (accounts && accounts.length > 0) {
        setWalletAddress(accounts[0])
        setIsConnected(true)
      }
    } catch (error) {
      console.error("Failed to connect wallet:", error)
    }
  }

  const disconnect = () => {
    if (peraWallet) {
      peraWallet.disconnect()
    }
    setWalletAddress(null)
    setIsConnected(false)
  }

  return (
    <WalletContext.Provider value={{ isConnected, walletAddress, connect, disconnect }}>
      {children}
    </WalletContext.Provider>
  )
}

export function useWallet() {
  const context = useContext(WalletContext)
  if (!context) {
    throw new Error("useWallet must be used within a WalletProvider")
  }
  return context
}
